import { ask_for_freight } from '$lib/Versies/Trucks'	

const coin_type = "0x1::aptos_coin::AptosCoin";

export const ask_APT_count = async ({
	address_hexadecimal_string
}) => {
	return await ask_for_freight ().dapp_network.view_G2 ({
		body: {
			"function": "0x1::coin::balance",
			"type_arguments": [
				coin_type
			],
			"arguments": [
				address_hexadecimal_string
			]
		}
	});
}